/* Controls */

div#controls {
    position: fixed;
    top: 6rem;
    @media (min-width: 992px) {
        top: 7rem;
    }
    right: 1rem;
    font-size: 80%;
    height: 94vh;
    width: max-content;
    @media (min-width: 992px) {
        width: 18%;
    }
}

div#controls h2 {
    font-size: 120%;
    margin: 0;
}

div#controls ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

div#navigation {
    border: thin solid gray;
    overflow-x: auto;
    overflow-y: auto;
    padding: 0.5em;
    position: absolute;
    top: 8rem;
    bottom: 5rem;
}

div#navigation li {
    margin-left: 1em;
}

div#navigation ul.inline {
    display: flex;
    flex-wrap: wrap;
}

div#navigation a {
    color: black;
}

div#navigation a.selected {
    font-weight: bold;
}

div#editing li {
    display: inline;
    font-size: 200%;
}

div#editing button {
    font-size: 100%;
    margin: 2px 2px 0 0;
    height: 1.5em;
}

div#editing button svg {
    vertical-align: unset;
}

div#citesearch input {
    width: 6em;
}

/* Front Page */
div#frontpage {
  margin-top: 1rem;
  position: relative;
  width: 70vw;
}

div#frontpage a {
  text-decoration: underline;
}

div#frontpage a:link,
div#frontpage a:visited,
div#frontpage:active {
  color: black;
  text-decoration: underline;
}
div#frontpage {
  margin: auto;
  max-width: 50vw;
}
.toc ul li {
  list-style: none;
}
.toc ul.inline {
  display: flex;
  flex-wrap: wrap;
}
.toc ul.inline li:not(:last-child)::after {
  white-space: pre;
  content: ', ';
}


/* Apparatus */

div.apparatus tei-app {
    display: inline;
    font-size: smaller;
}

div.apparatus tei-app.app-copy {
    display: none;
}

div.apparatus tei-app span.source {
    font-weight: normal;
    font-style: italic;
}

div.apparatus tei-app>span.lem {
    font-weight: bold;
}

div.apparatus tei-app>span.lem:before {
    content: "\a";
    white-space: pre;
}

span.note {
    display: none;
}

span.pnum {
    font-weight: bold;
}

div.apparatus span.note {
    display: inline;
}

div.apparatus tei-app>tei-lem {
    font-weight: bold;
    display: inline;
}

div.apparatus tei-app tei-rdg {
    display: inline;
}

div.apparatus tei-app tei-rdg.hidden {
    display: none;
}

div.apparatus tei-app tei-rdg:before {
    content: " ";
}

div.apparatus tei-rdgGrp {
    display: inline;
}

div.apparatus tei-app tei-wit {
    display: inline;
    font-style: italic;
}

div.apparatus tei-app>tei-note {
    display: inline;
    font-style: italic;
}


/*TODO: figure out what to do with source lists.*/

tei-div[type="sources"] {
    /* display: none; */
}

tei-div button.app {
    border: thin solid white;
    background-color: white;
    font-size: 120%;
    width: 25px;
    height: 20px;
    padding: 0px;
}

tei-div button.app:hover {
    border: thin solid silver;
}

tei-div button.note:hover {
    border: thin solid white;
}

button:focus {
    outline: none;
}

tei-div span.apps {
    display: inline;
    position: absolute;
    right: 1em;
}

tei-p span.apps {
    display: inline;
    position: absolute;
    right: -5rem;
    top: 0px;
    width: 80px;
}


/* Apparatus tooltips */

div.apparatus span.lineref {
    display: none;
}

.ui-tooltip {
    width: 60rem;
}

.ui-tooltip-content div.apparatus span.source {
    font-style: italic;
    font-weight: normal;
}

.ui-tooltip-content div.apparatus>span.lem {
    display: none;
}

.ui-tooltip-content div.apparatus>span.lineref {
    display: inline;
}

.ui-tooltip-content div.apparatus tei-lem {
    display: inline;
}

.ui-tooltip-content div.apparatus tei-rdg {
    display: inline;
}

.ui-tooltip-content div.apparatus tei-rdg.hidden {
    display: none;
}

.ui-tooltip-content div.apparatus tei-rdg:before {
    content: " ";
}

.ui-tooltip-content div.apparatus tei-rdgGrp {
    display: inline;
}

.ui-tooltip-content div.apparatus tei-wit {
    display: inline;
    font-style: italic;
    margin-right: 0.3em;
}

.ui-tooltip-content div.apparatus tei-witdetail {
    display: none;
}

.ui-tooltip-content div.apparatus tei-note {
    display: inline;
    font-style: italic;
    margin-right: 0.3em;
}

.ui-tooltip-content span.note {
    display: inline;
}

.ui-tooltip-content span.separator {
    display: none;
}

/* lists in tooltips */

.ui-tooltip-content tei-listwit {
    display: inline;
    margin: 0;
}

.ui-tooltip-content tei-listbibl {
    display: inline;
    margin: 0;
}

.ui-tooltip-content tei-head {
    font-size: 1em;
    font-weight: bold;
    margin-left: 0.3em;
}

.ui-tooltip-content tei-witness {
    display: inline;
}

.ui-tooltip-content tei-witdetail {
    display: none;
}

.ui-tooltip-content tei-bibl {
    display: inline;
}

.highlight {
    background: #00ffab;
}

.highlight tei-lem:empty:before {
    content: " – ";
}


/* Apparatus dialogs */

.ui-dialog-titlebar {
    padding: 0;
}

.ui-dialog-content tei-lem {
    display: inline;
}

.ui-dialog-content tei-lem:hover {
    cursor: pointer;
    text-decoration: underline;
}

.ui-dialog-content tei-rdg {
    display: inline;
}

.ui-dialog-content tei-rdg.hidden {
    display: none;
}

.ui-dialog-content tei-rdg:hover {
    cursor: pointer;
    text-decoration: underline;
}

.ui-dialog-content tei-rdgGrp {
    display: inline;
}

.ui-dialog-content tei-wit {
    display: inline;
    font-style: italic;
    margin-right: 0.3em;
}

.ui-dialog-content tei-witdetail {
    display: none;
}

.ui-dialog-content span.lem {
    display: none;
}

.ui-dialog-content tei-note[data-id]:hover {
    cursor: pointer;
    text-decoration: underline;
}

.ui-dialog-content span[data-id]:hover {
    cursor: pointer;
    text-decoration: underline;
}

.ui-dialog-content tei-note {
    display: inline;
    font-style: italic;
    margin-right: 0.3em;
}

.ui-dialog-content span.source {
    font-weight: normal;
}


/* Choice elements */

tei-choice tei-abbr+tei-expan:before,
tei-choice tei-expan+tei-abbr:before,
tei-choice tei-sic+tei-corr:before,
tei-choice tei-corr+tei-sic:before,
tei-choice tei-orig+tei-reg:before,
tei-choice tei-reg+tei-orig:before {
    content: " (";
}

tei-choice tei-abbr+tei-expan:after,
tei-choice tei-expan+tei-abbr:after,
tei-choice tei-sic+tei-corr:after,
tei-choice tei-corr+tei-sic:after,
tei-choice tei-orig+tei-reg:after,
tei-choice tei-reg+tei-orig:after {
    content: ")";
}


/* A */

tei-ab {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-accmat {
    display: block;
}

tei-accmat:before {
    content: "accompanying materials: ";
}

tei-acquisition {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-acquisition:before {
    font-weight: bold;
    content: "acquisition";
}

tei-activity {
    display: block;
}

tei-activity:before {
    content: "activity: ";
}


/* add */


/* Could add other [place=] variations */

tei-add[place="above"] {
    vertical-align: super;
}

tei-add[place="inline"]:before {
    content: "«";
}

tei-add[place="inline"]:after {
    content: "»";
}

tei-add[place="inRas"]:before {
    content: "«";
}

tei-add[place="inRas"]:after {
    content: "»";
}

tei-add[place="below"] {
    vertical-align: sub;
}

tei-additional {
    display: block;
}

tei-additions {
    display: block;
}

tei-additions:before {
    content: "Significant additions within the document: ";
}

tei-addrline {
    display: block;
}

tei-address[data-tei-rend~="block"],
addresss[data-tei-rendition~="#block"] {
    display: block;
}

tei-admininfo {
    display: block;
}

tei-app {
    display: inline;
}

tei-app tei-note {
    display: none;
}

tei-appnfo {
    display: block;
}

tei-appinfo:before {
    content: "Application information: ";
}

tei-application {
    display: block;
}

tei-application:after {
    content: " " attr(version);
}

tei-argument {
    display: block;
}

tei-att {
