}

tei-binding[data-tei-contemporary]:before {
    content: "Binding: \A""Contemporary: " attr(contemporary) "\A";
    white-space: pre-wrap;
}

tei-bindingdesc {
    display: block;
}

tei-bindngdesc:before {
    content: "Binding Description: \A";
}

tei-birth {
    display: block;
}

tei-birth:before {
    content: "Birth: ";
}

tei-byline {
