}

tei-fileDesc>extent {
    display: none;
}


/* f */

tei-figure {
    display: block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}


/* g */

tei-gap[unit="lines"] {
    display: block;
    margin-left: 3em;
}

tei-gi {
    font-family: Courier, monospace;
}

tei-gi:before {
    content: "<";
}

tei-gi:after {
    content: ">";
}


/* h */

tei-head {
    display: block;
    font-family: Arvo, sans-serif;
    font-weight: normal;
}

div>tei-head {
    font-size: 180%;
}

tei-div.textpart {
    display: none;
}

tei-div>tei-head {
    font-size: 170%;
}

tei-div>tei-div>tei-head {
    font-size: 155%;
}

tei-div>tei-div>tei-div>tei-head {
    font-size: 140%;
}

tei-div>tei-div>tei-div>tei-div>tei-head,
list>tei-head {
    font-size: 130%;
}

tei-div>tei-div>tei-div>tei-div>tei-div>tei-head {
    font-size: 120%;
}

tei-div>tei-div>tei-div>tei-div>tei-div>tei-div tei-head {
    font-size: 110%;
}

tei-figure>tei-head {
    display: block;
    margin: 1.5em auto 1.5em auto;
    padding: 0.5em 1.5em 1.5em 1.5em;
    border: 1px solid black;
    border-radius: 15px;
    width: 325px;
    position: relative;
    border: 1px solid #aaaaaa;
    background: #fafafa;
    /* reset text-indent for note with <l> ancestors. */
    text-indent: 0;
    font-size: 85%;
    text-align: justify;
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
    width: 85%;
    font-family: Georgia, Serif;
    font-size: 13pt;
}

tei-foreign {
    font-style: italic;
}

tei-hi[rend="bold"] {
    font-weight: bold;
}

tei-hi[rend="caps"] {
    font-weight: bold;
}

tei-hi[rend="italic"] {
    font-style: italic;
}

tei-hi[rend="normal"] {
    font-style: normal;
    text-decoration: none;
    font-weight: normal;
}

tei-hi[rend="super"] {
    font-size: 60%;
    vertical-align: super;
}

tei-hi[rend="superscript"] {
    font-size: 60%;
    vertical-align: super;
}


/* i */

