    font-family: Courier, monospace;
}

tei-incipit {
    font-style: italic;
}

tei-item {
    display: list-item;
    margin-left: 1em;
}

tei-cell item {
    margin-left: 1em;
}


/* j */


/* k */


/* l */

tei-l {
    display: block;
    margin-left: 4em;
}

tei-l[data-num]:before {
    bottom: -.3em;
    content: attr(data-num);
    display: block;
    float: left;
    font-size: .8em;
    margin-left: -10vw;
    position: relative;
    text-align: right;
    width: 3em;
}

tei-l tei-label {
    display: block;
    float: left;
    margin-left: -7em;
    text-align: right;
    width: 4em;
    font-weight: bold;
}

tei-lb:after {
    content: "\a";
    white-space: pre;
}

tei-lg {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-lg tei-label {
    position: absolute;
    left: 4em;
    font-weight: bold;
}

tei-list {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    list-style-type: none;
}

div#tei tei-div tei-list {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    list-style-type: none;
    width: 80%;
}

tei-item>tei-list {
    margin-top: 0;
}

tei-sourceDesc tei-list {
    list-style-type: none;
}

tei-sourceDesc>tei-list>tei-head {
    font-size: 140%;
    font-weight: bold;
}

tei-sourceDesc tei-list>tei-item {
    display: list-item;
}


/* Match only lists that are descendants of
tei-text. Otherwise they show up in the header. */

tei-text tei-list[type="simple"] {
    list-style-type: none;
}

tei-text tei-list[type="bulleted"] {
    list-style-type: disc;
}

tei-text tei-list[type="ordered"] {
    list-style-type: none;
    counter-reset: items 0;
}

tei-text tei-list[type="ordered"]>tei-item {
    display: list-item;
    margin-left: 0;
}

tei-item>tei-list[type="ordered"] {
    margin-left: 1em;
}

tei-text tei-list[type="ordered"]>item:before {
    counter-increment: items 1;
    content: counter(items, decimal) ". ";
}

tei-text tei-list[type="gloss"] {
    list-style-type: none;
}

tei-list[type="gloss"]>tei-label {
    display: block;
    margin-top: 0.25em;
    margin-bottom: 0.25em;
    font-weight: bold;
}

tei-list[type="gloss"]>tei-item {
    margin-left: 2em;
}

tei-item>tei-list[type="ordered"] {
    margin-left: 1em;
}

tei-listapp {
    display: block;
    margin: 1em;
    padding: 1em;
}


/* sourceDesc lists */

