/* q */

tei-q {
    quotes: "\201c""\201d""\2018""\2019""\201c""\201d""\2018""\2019""\201c""\201d""\2018""\2019""\201c""\201d";
}

tei-q:before {
    content: "";
}

tei-q:after {
    content: "";
}

tei-q[data-tei-next]:after {
    content: "" !important;
}

tei-q[data-tei-prev]:before {
    content: "" !important;
}

tei-quote {
    font-style: italic;
}

tei-quote[rend="blockquote"] {
    display: block;
    margin: 1em;
    text-align: justify;
}


/* r */

tei-app tei-rdg {
    display: none;
}

tei-re {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-recordhist {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-ref {
    color: black;
    text-decoration: underline;
    text-decoration-style: dotted;
}

tei-ref a {
    color: black;
    text-decoration: underline;
    text-decoration-style: dotted;
}

tei-remarks {
    font-weight: bold;
}

tei-remarks:before {
    font-weight: bold;
    content: "remarks: ";
}

tei-respStmt {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-revisiondesc {
    display: none;
}


/* s */

tei-s {
    text-decoration: none;
}

tei-salute {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-samplingdecl {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-scriptnote {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-seal {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-seal:before {
    font-weight: bold;
    content: "Seal: ";
}

tei-sealdesc {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-secfol:before {
    font-weight: bold;
    content: "Second Folio: ";
}

tei-secl>*:first-child:before {
    content: "\a[";
    white-space: pre;
}

tei-secl>*:last-child:after {
    content: "]\a\a";
    white-space: pre;
}

tei-seg {
    /* Empty Set Rule */
    outline: 0px solid transparent;
}

tei-sense {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-seriesStmt {
    display: none;
}

tei-setting {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-signed {
    display: block;
    margin-top: 2em;
}

tei-socalled {
    quotes: "\201c""\201d""\2018""\2019""\201c""\201d""\2018""\2019""\201c""\201d""\2018""\2019""\201c""\201d";
}

tei-socalled:before {
    content: open-quote;
}

tei-socalled:after {
    content: close-quote;
}

tei-socalled[data-tei-next]:after {
    content: "" !important;
}

tei-socalled[data-tei-prev]:before {
    content: "" !important;
}

tei-sound {
    font-style: italic;
}

tei-source {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-sourcedesc {
    display: none;
}

tei-space:before {
    font-weight: bold;
    content: "";
}

tei-span[data-tei-data-n]:before {
    content: attr(data-n);
}

tei-tei tei-egxml tei-span {
    display: inline;
}

tei-speaker {
    display: block;
    font-weight: bold;
    height: 24px;
    margin-right: 0;
}

tei-sponsor {
    display: block;
    margin-top: 0;
    margin-bottom: 0;
}

tei-sponsor:before {
    font-weight: bold;
    content: "Sponsor:";
}

tei-stage {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
    font-style: italic;
}

tei-stamp {
    display: block;
    margin-top: 1em;
    margin-bottom: 1em;
}

tei-stamp:before {
    font-weight: bold;
    content: "Stamped: ";
}


/* t */

tei-tei {
    display: block;
}


/* Tables */

table {
    border-collapse: collapse;
    display: table;
    margin-top: 2em;
    margin-bottom: 2em;
    font-size: 12pt;
}

tr {
    display: table-row;
}

tr[role="label"] {
    font-weight: bold;
}

td {
    padding: 1em 1.5em 1em 1em;
    border: thin solid gray;
}


/* for cell or row with @role = label */

td[role="label"] {
    font-weight: bold;
}

caption {
    font-weight: bold;
}

tei-table>tei-head {
    display: table-caption;
}

:host(tei-table)>tei-head {
    display: table-caption;
}

tei-teiHeader {
    display: none;
    margin-top: 2em;
    margin-bottom: 2em;
}

tei-tei>tei-text {
    display: block;
    line-height: 1.5;
    margin-top: auto;
    margin-right: auto;
    margin-bottom: auto;
    margin-left: 1rem;
    @media (min-width: 992px) { 
        margin-left: auto;
     };
    max-width: 50vw;
}

tei-textclass {
    display: none;
}

tei-textdesc {
    display: none;
}


/* title */

tei-tei tei-title {
    display: inline;
}

tei-title[level="m"] {
    font-style: italic;
}

tei-bibl > tei-title {
  font-style: italic;
}

tei-titleStmt>tei-title {
    display: block;
    font-family: Arvo, sans-serif;
    font-weight: normal;
    font-weight: bold !important;
    font-size: 150%;
}

tei-typeDesc {
    display: none;
}


/* u */

tei-u:before {
    content: "-";
}


/* v */

tei-view {
    font-style: italic;
}


/* w */

tei-wit {
    display: none;
}

tei-witness>tei-abbr {
    font-weight: bold;
}

tei-witness>tei-title {
    font-weight: bold;
}

tei-witdetail {
    display: none;
}


/* x */


/* z */


/* styles for html shell and html elements in TEI (e.g.,<a> and <img>) */

html>body {
    margin: 0;
    padding: 0;
}

html {
    margin: 0;
    padding: 0;
    font-family: "Lucida Grande", "Cardo", "Arial Unicode MS", "Galilee Unicode Gk", "New Athena Unicode", "Athena Unicode", "Palatino Linotype", "Titus Cyberbit Basic", "Vusillus Old Face", "Alphabetum", "Galatia SIL", "Code 2000", "GentiumAlt", "Gentium", "Minion Pro", "GeorgiaGreek", "Vusillus Old Face Italic", "Everson Mono", "Aristarcoj", "Porson", "Legendum", "Aisa Unicode", "Hindsight Unicode", "Caslon", "Verdana", "Tahoma";
    font-size: 12pt;
    background-color: white;
    color: #292929;
}

div#tei {
    margin-top: 1rem;
    position: relative;
    width: 70vw;
    @media (min-width: 768px) {
        width: auto;
    }
}

div#tei a {
    text-decoration: underline;
}

div#tei a:link,
div#tei a:visited,
div#teia:active {
    color: black;
    text-decoration: underline;
}

div#prevNext {
    width: 70vw;
    @media (min-width: 768px) {
        width: auto;
    }
    text-align: center;
    font-size: larger;
    margin-top: 1rem;
}
div#prevNext a {
    color: black;
    text-decoration: underline;
    text-decoration-style: dotted;
}

#footer {
    margin-top: 1em;
    border-top: 1px solid #292929;
    padding-top: 0.5em;
    font-size: 12pt;
}

.hidden {
    display: none;
}

div.notes {
  max-width: 70vw;
  @media (min-width: 992px) {
      width: auto;
  }
  margin-top: 2em;
}

ol.notes {
  border-top: thin solid silver;
    list-style: none;
    margin: auto;
    max-width: 50vw;
}

ol.notes>li>a {
    display: block;
    float: left;
    font-family: Courier, monospace;
    margin-left: -4em;
    text-align: right;
    width: 3em;
}

ol.notes tei-p {
    text-align: left;
}

ol.notes a {    
    color: black;
}


/* xml-serialization */

.eg-element {
    color: #0165c8;
}

.eg-comment {
    color: gray;
}

.eg-att,
.eg-nsdecl {
    color: #f58650;
}

.eg-attvalue,
.eg-ns {
    color: #993300;
}

.eg-text {
    color: #292929;
}

label {
    @media (min-width: 576px) {
        margin-left: 0.25rem;
    }
}


/* -----
SVG Icons - svgicons.sparkk.fr
----- */

.svg-icon {
    width: 1em;
    height: 1em;
}

.svg-icon path,
.svg-icon polygon,
.svg-icon rect {
    fill: #005cb9;
}

.svg-icon circle {
    stroke: #005cb9;
    stroke-width: 1;
}
